.markdown-content {
  color: #fff;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.8);
  font-size: 1.75em;
  padding: 1vw 20vw;
}

.toc-parent {
  display: flex;
  justify-content: center; /* Center the child horizontally */
}

.toc-container {
  color: #adadad00;
  margin: auto;
  font-size: 0.75em;
  padding: 1vh;
  text-decoration: none; /* Remove underline */
  display: inline-block; /* Occupy only the necessary space */
  /* Additional styles as needed */
}

.grid-container {
  display: grid;
  flex-direction: row; /* Arrange items horizontally */
  gap: 1em; /* Adjust the gap between items */
  padding-bottom: 3vh;
  grid-template-columns: repeat(auto-fit, minmax(45%, 1fr)); /* Two columns with a minimum width of 45% each */
}

@media (max-width: 1000px) {
  .grid-container {
    grid-template-columns: 1fr; /* Switch to one column when width is <= 600px */
  }
}

.left-cell {
  flex: 1; /* Allow the left cell to grow and take up available space */
  max-height: 100%; /* Set max height to match the smaller column's height */
}

.right-cell {
  flex: 1; /* Allow the right cell to grow and take up available space */
  max-height: 100%; /* Set max height to match the smaller column's height */
  overflow: auto; /* Add overflow for content that exceeds max-height */
  background-color: rgba(41, 41, 41, 0.377);
}

a:link {
  text-decoration: none;
  color: rgb(241, 241, 241);
  font-style: italic;
}

a:visited {
  text-decoration: none;
  color: rgb(153, 153, 153);
  font-style: italic;
}

a:hover {
  color: rgb(0, 150, 137);
  font-style: italic;
}

a:active {
  text-decoration: underline;
  color: rgb(0, 150, 137);
  font-style: italic;
}

.markdown-content h1,
.markdown-content h2,
.markdown-content h3,
.markdown-content h4,
.markdown-content h5,
.markdown-content h6 {
  color: #fff;
}

.markdown-content p,
.markdown-content li {
  color: #fff;
}

.markdown-content img {
  display: inline-block;
}

.markdown-content code {
  font-size: 1em;
}

.markdown-content strong {
  padding: 4px;
  border-radius: 5%;
  background: #2e2e2ea8;
  font-family: "Courier New", monospace;
}

.fade-in-blogpost {
  opacity: 0;
  animation: fadeIn 0.3s forwards ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.blog-title-selected {
  font-size: 2em;
  font-family: "WiiFont", sans-serif;
  color: #fff;
  padding-top: 3vh;
  padding-bottom: 3vh;
}

.TOC-title {
  font-size: 1.5em;
  font-family: "WiiFont", sans-serif;
  color: #ffffff;
  text-align: center;
}

.date-selected {
  font-size: 1.25em;
  font-family: "WiiFont", sans-serif;
  color: #fff;
  text-align: center;
  padding: 2vh;
}

.blog-tagline-selected {
  font-size: 1.25em;
  font-family: "WiiFont", sans-serif;
  color: #fff;
  padding-top: 1vh;
  padding-left: 3vh;
  padding-right: 3vh;
}

.horizontal-line {
  padding-top: 2vh;
  border-top: 5px solid #fff;
}

.blog-post-tags {
  padding-top: 2vh;
  display: flex;
  justify-content: center;
}

.blog-post-tag-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.blog-post-tag {
  background-color: #33333379;
  padding: 8px 12px;
  border-radius: 4px;
  text-align: center;
  font-size: 0.75em;
  color: #ddd;
}
